import { TAG_VIEW } from '@/constant';
import { getItem, setItem } from '@/utils/storeage';

var state = function state() {
  return {
    tagViewList: getItem(TAG_VIEW) || [],
    // tag
    currentRoute: '' // 

  };
};

var mutations = {
  // 
  ADD_TAG_VIEW_LIST: function ADD_TAG_VIEW_LIST(state, tag) {
    // 
    state.currentRoute = tag.fullPath; // 

    var route = state.tagViewList.find(function (x) {
      return x.path === tag.path;
    }); // 

    if (!route) {
      state.tagViewList.push(tag); // 

      setItem(TAG_VIEW, state.tagViewList);
    }
  },
  // 
  DELETE_TAG_VIEW: function DELETE_TAG_VIEW(state, index) {
    state.tagViewList.splice(index, 1);
    setItem(TAG_VIEW, state.tagViewList);
  },
  // ,
  DELETE_ALL_TAG: function DELETE_ALL_TAG(state) {
    state.tagViewList.splice(1, state.tagViewList.length - 1);
    setItem(TAG_VIEW, state.tagViewList);
  },
  // 
  DELETE_OTHER_TAG: function DELETE_OTHER_TAG(state, item) {
    state.tagViewList = state.tagViewList.filter(function (x) {
      return x.meta.affix || x.path === item.path;
    });
    setItem(TAG_VIEW, state.tagViewList);
  }
};
export default {
  namespaced: true,
  state: state,
  mutations: mutations
};