import { LANG } from '@/constant';
import { getItem, setItem } from '@/utils/storeage';

var state = function state() {
  return {
    sidebarOpened: true,
    language: getItem(LANG) || 'zh' // 

  };
};

var mutations = {
  TRIGGER_SIDEBAR_OPENED: function TRIGGER_SIDEBAR_OPENED(state) {
    state.sidebarOpened = !state.sidebarOpened;
  },
  // 
  SET_LANGUAGE: function SET_LANGUAGE(state, lang) {
    // local
    setItem(LANG, lang); // 

    state.language = lang;
  }
};
export default {
  namespaced: true,
  state: state,
  mutations: mutations
};