import { SELECT_COLOR, DEFAULT_COLOR } from '@/constant';
import { getItem, setItem } from '@/utils/storeage';
import variables from '@/styles/variables.scss';

var state = function state() {
  return {
    variables: variables,
    isCurrentColor: getItem(SELECT_COLOR) || DEFAULT_COLOR // 

  };
};

var mutations = {
  SET_CURRENT_COLOR: function SET_CURRENT_COLOR(state, newColor) {
    state.isCurrentColor = newColor; // 

    setItem(SELECT_COLOR, newColor); // menuBggetters

    state.variables.menuBg = newColor;
  }
};
export default {
  namespaced: true,
  state: state,
  mutations: mutations
};